<template>
  <Logo />
  <div class="wrap">
    <div class="contents">
      <h2 class="content-title">{{ $t('top.recommend') }}</h2>
      <div class="item-wrap">
        <RecommendItem
          v-for="(recommend, index) in courses"
          :key="index"
          :id="recommend.id"
          :image="recommend.image"
          :title="recommend.title"
          :teacherName="recommend.Instructor.name"
          class="item"
        />
      </div>
    </div>
    <div class="contents">
      <h3 class="content-title">{{ $t('top.category') }}</h3>
      <div class="item-wrap">
        <category-item
          v-for="(category, index) in categories"
          :key="index"
          :category-name="category.name"
          :image="category.image"
          class="item"
        />
      </div>
    </div>
    <div class="contents">
      <h3 class="content-title">{{ $t('top.teacher') }}</h3>
      <div class="item-wrap">
        <TeacherItem
          v-for="(instructor, index) in instructors"
          :key="index"
          :image="instructor.icon"
          :teacherName="instructor.name"
          :id="instructor.id"
          class="item"
        />
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import homeQuery from '@/graphql/queries/home.query'
import { HomeQuery } from '@/graphql/@types/types'
import Logo from '@/components/Logo.vue'
import CategoryItem from '@/components/CategoryItem.vue'
import RecommendItem from '@/components/RecommendItem.vue'
import TeacherItem from '@/components/TeacherItem.vue'

export default defineComponent({
  components: {
    Logo,
    CategoryItem,
    RecommendItem,
    TeacherItem
  },
  setup() {
    const { result } = useQuery<HomeQuery>(homeQuery)
    const courses = useResult(result, null, (data) => data.Courses)
    const categories = useResult(result, null, (data) => data.Categories)
    const instructors = useResult(result, null, (data) => data.Instructors)

    return {
      courses,
      categories,
      instructors
    }
  }
})
</script>

